  
body{
    background-image: linear-gradient(120deg,#487eb0,#273c75);
    margin: 0;
    font-family: Helvetica;
    color: white;
  }
  .name{
      font-size: 1.7em;
      color: rgb(10, 10, 10);
      font-weight: bold;
      font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  
  }
  .corona-body{
    height: 100vh;
    width: 100vw;
    background-image: linear-gradient(120deg,#70a1ff,#1e90ff);
  }
  .confirm{
      background-color:#4cd137 ;
      border: 1 px solid #34495e ;
    
    }
    .c-title{
  font-size: 1.7em;
  font-weight: bold; 
  color: #000000;
  font-family: "Lucida Console", Courier, monospace;
  }
    .c-body{
        font-weight: bold;
    }
    
    .recovered{
      background-color: #badc58 ;
      border: 1 px solid #34495e;
    
    }
    .box{
        border-radius: 25px;
        box-shadow: 10px 10px 8px #04332f;
        
    }
    .card-decks{
        margin-top: 4em;
    }
    
    .deaths{
      background-color: #e84118 ;
      border: 1 px solid #34495e;
    
    }
    
  .lastupdate{
    font-size: 1em;
    color: rgb(5, 5, 5);
  }
  .number{
    font-size: 2.5em;
  }
  .judul{
    background-color:#30336b;
    color:aliceblue;
    display: inline-block;
    padding: 10px 10px;
